import React, { useState } from 'react';
import { AreaChart, Area, XAxis, YAxis, CartesianGrid, Tooltip, ResponsiveContainer, Legend } from 'recharts';

// Placeholder data
const weeklyData = [
  { name: 'Mon', Opens: 2200, Clicks: 1300 },
  { name: 'Tue', Opens: 2400, Clicks: 1500 },
  { name: 'Wed', Opens: 1800, Clicks: 1100 },
  { name: 'Thu', Opens: 2800, Clicks: 1900 },
  { name: 'Fri', Opens: 2100, Clicks: 1400 },
  { name: 'Sat', Opens: 3500, Clicks: 2200 },
  { name: 'Sun', Opens: 3200, Clicks: 2000 },
];

const monthlyData = [
    { name: 'Week 1', Opens: 12000, Clicks: 7000 },
    { name: 'Week 2', Opens: 15000, Clicks: 9000 },
    { name: 'Week 3', Opens: 11000, Clicks: 6500 },
    { name: 'Week 4', Opens: 18000, Clicks: 11000 },
];


const CustomTooltip = ({ active, payload, label }) => {
    if (active && payload && payload.length) {
      return (
        <div className="bg-primary p-4 rounded-lg border border-border shadow-lg">
          <p className="label text-text-secondary">{`${label}`}</p>
          <p className="intro text-accent">{`Opens : ${payload[0].value}`}</p>
          <p className="intro text-secondary">{`Clicks : ${payload[1].value}`}</p>
        </div>
      );
    }
  
    return null;
};

const CombinedOverview = () => {
    const [timeframe, setTimeframe] = useState('weekly');
    const data = timeframe === 'weekly' ? weeklyData : monthlyData;

  return (
    <div className="bg-primary p-6 rounded-xl border border-border">
      <div className="flex justify-between items-center mb-4">
        <h2 className="text-xl font-bold text-text">Performance Overview</h2>
        <div className="flex space-x-2">
            <button 
                onClick={() => setTimeframe('weekly')}
                className={`px-3 py-1 text-sm font-semibold rounded-md transition ${timeframe === 'weekly' ? 'bg-accent text-background' : 'bg-transparent text-text-secondary hover:bg-border'}`}
            >
                Weekly
            </button>
            <button 
                onClick={() => setTimeframe('monthly')}
                className={`px-3 py-1 text-sm font-semibold rounded-md transition ${timeframe === 'monthly' ? 'bg-accent text-background' : 'bg-transparent text-text-secondary hover:bg-border'}`}
            >
                Monthly
            </button>
        </div>
      </div>
      <div style={{ width: '100%', height: 400 }}>
        <ResponsiveContainer>
          <AreaChart
            data={data}
            margin={{ top: 10, right: 30, left: 0, bottom: 0 }}
          >
            <defs>
              <linearGradient id="colorOpens" x1="0" y1="0" x2="0" y2="1">
                <stop offset="5%" stopColor="#34D399" stopOpacity={0.8}/>
                <stop offset="95%" stopColor="#34D399" stopOpacity={0}/>
              </linearGradient>
              <linearGradient id="colorClicks" x1="0" y1="0" x2="0" y2="1">
                <stop offset="5%" stopColor="#A855F7" stopOpacity={0.8}/>
                <stop offset="95%" stopColor="#A855F7" stopOpacity={0}/>
              </linearGradient>
            </defs>
            <XAxis dataKey="name" stroke="#A0A0A0" />
            <YAxis stroke="#A0A0A0" />
            <CartesianGrid strokeDasharray="3 3" stroke="#2D2A4C" />
            <Tooltip content={<CustomTooltip />} />
            <Legend wrapperStyle={{ color: '#E0E0E0' }}/>
            <Area type="monotone" dataKey="Opens" stroke="#34D399" fillOpacity={1} fill="url(#colorOpens)" />
            <Area type="monotone" dataKey="Clicks" stroke="#A855F7" fillOpacity={1} fill="url(#colorClicks)" />
          </AreaChart>
        </ResponsiveContainer>
      </div>
    </div>
  );
};

export default CombinedOverview;